<template>
  <div>
    <table border="0">
      <thead>
        <tr>
          <th v-for="(item, index) in ths" :key="index">
            <input type="text" v-model="ths[index]" disabled>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in data" :key="index">
          <td v-for="(kid, idx) in item" :key="idx">
            <input type="text" v-model="item[idx]" @change="handleInputChange(index)">
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  export default {
    model: {
      prop: 'tbs'
    },
    props: {
      ths: {
        type: Array
      },
      tbs: {
        type: Array
      }
    },
    data () {
      return {
        data: []
      }
    },
    watch: {
      /* data: {
        deep: true,
        handler () {
          console.log('data change')
          this.$emit('input', this.data)
        }
      } */
    },
    created () {
      this.init()
    },
    methods: {
      init () {
        if (!this.tbs || this.tbs.length === 0) {
          this.data = [{
            key: '',
            title: ''
          }]
        }
        // 注意tbs键的顺序要跟ths保持一致 to-do
      },
      handleInputChange (index) {
        if (!this.data[index + 1]) {
          this.data.push({
            key: '',
            title: ''
          })
        }
        this.$emit('input', this.data2json(this.data))
      },
      data2json (data) {
        let temp = []
        for (let i = 0; i < data.length - 1; i++) {
          temp[i] = data[i]
        }
        return temp
      }
    }
  }
</script>

<style scoped>

</style>


