<template>
  <div>
    <div class="eson-editor-wrapper" ref="eson-editor" contenteditable="true"></div>
  </div>
</template>

<script>
import EsonEditor from '../tools/eson-editor'
export default {
  components: {
  },
  data () {
    return {
    }
  },
  mounted() {
    let esonEditor = new EsonEditor(this.$refs['eson-editor'])
  },
  methods: {

  }
}
</script>

<style>
.eson-editor-wrapper{
  border: 1px solid #ccc;
  height: 300px;
  text-align: left;
  color: rgb(206, 145, 120);
  background: #1E1E1E;
  font-size: 14px;
  margin-bottom: 10px;
}
.eson-editor-row{
  height: 22px;
  line-height: 22px;
}
</style>



