<template>
  <div class="edit-wrapper">
    <div class="left">
      <Input v-model="data.key" placeholder="组件/区块名称" @on-change="tableChange"/>
    </div>
    <div class="right">
      <div v-for="(item, index) in data.items" :key="index" class="item">
        <div>
          <Input
            placeholder="key"
            v-model="item.key"
            class="left-item"
            @on-change="handleInputChange(index)"/>
        </div>
        <div>
          <Input
            placeholder="value"
            v-model="item.value"
            class="right-item"
            @on-change="handleInputChange(index)"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    data1: {
      type: Object
    },
    index: {
      type: Number
    },
    value: {
      type: Object
    }
  },
  data () {
    return {
      key: '',
      data: this.value
    };
  },
  created () {
    if (!this.data){
      this.data = {
        key: '',
        items: [{
          key: '',
          value: ''
        }]
      }
    }
  },
  computed: {},
  methods: {
    handleInputChange(index){
      if(!this.data.items[index+1]&&(this.data.items[index].key === ''|| this.data.items[index].value === '')){
        this.data.items.push({
          key: '',
          value: ''
        })
      }
      this.$emit('input', this.data)
      this.$emit('edit-table-change', this.index)
    },
    tableChange(){
      this.$emit('input', this.data)
      this.$emit('edit-table-change', this.index)
    }
  },
}
</script>

<style scoped>
*{
  box-sizing: border-box;
}
.edit-wrapper{
  display: flex;
  width: 325px;
  box-sizing: border-box;
  min-height: 105px;
  border: 1px solid rgb(151, 148, 148);
}
.left{
  display: flex;
  padding: 0 10px 0 10px;
  align-items: center;
  justify-content: center;
  min-width: 120px;
  border-right: 1px solid rgb(151, 148, 148);
}
.right{
  min-width: 200px;
  border: 1px solid #ccc;
}
.item{
  padding: 0 5px 0 5px;
  display: flex;
}
.left-item{
  width: 100%;
}
.right-item{
  width: 100%;
}
input{
  width: 100px;
  height: 25px;
  background: rgb(218, 213, 210);
}
</style>


