let MyPlugin= {}
function $t(x){
  alert(x*x*x)
}
MyPlugin={
  install : function (Vue, opt) {
    Vue.prototype.$t = $t
    Vue.component('xx', {
      data() {
       return {
        count: 0,
       } 
      },
      created(){
        console.log('from xx')
      },
      template:'<button v-on:click="count++">点击计算点击次数：{{count}}次</button>'
    })
  }
}
export default MyPlugin


