<template>
  <div class="lan-wrapper">
    <div class="lan-left">
      <div class="left-header" @click="handleNewPage">
        <Icon type="md-add" style="margin-right: 5px"/>New Page
      </div>
      <ul v-if="pages.length > 0" @selectstart.prevent>
        <li
          v-for="(item, index) in pages"
          :key="index"
          v-contextmenu:contextmenu
          @contextmenu="test(item)"
          :class="{'page-item': 'true', 'active-page-item': curPage.id === item.id ? true : false }"
          @click="handleItemClick(item)"
          :title="item.name">
          {{ item.name }}
        </li>
        <v-contextmenu ref="contextmenu" @contextmenu="handleContextmenu">
          <v-contextmenu-item>重命名</v-contextmenu-item>
          <v-contextmenu-item @click="handlePageDelete">删除</v-contextmenu-item>
        </v-contextmenu>
      </ul>
      <div style="margin-top: 40px" v-else>
        no page
        <p style="color: #FD783B" @click="handleNewPage">Click here to add page!</p>
      </div>
    </div>
    <div class="lan-middle">
      <div class="lan-middle-nocontent" v-if="curPage === null">Choose or Select a Page!</div>
      <div class="lan-middle-content" v-else>
        <div style="margin: 0 0 10px 0">
          <Button @click="clear" type="primary" class="button">清除</Button>
          <Button @click="save" type="primary" class="button">保存</Button>
          <Button @click="set" type="primary" class="button">配置导出</Button>
          <Button @click="change" type="primary" class="button">转换</Button>
          <Button @click="multi" type="primary" :loading="multiLoading">生成多语言</Button>
          <Button @click="download" type="primary" class="button">下载</Button>
        </div>
        <template v-if="curPage !== null">
          <EditableTable
            v-for="(item, index) in curPage.data"
            :key="curPage.id + index"
            v-model="curPage.data[index]"
            :index='index'
            class="edit-table"/>
        </template>
        <!-- <Button icon="md-add-circle" type="primary"></Button> -->
        <Icon type="md-add-circle" size="20" style="margin-top: 10px" @click="handleTableAdd"/>
      </div>
    </div>
    <div class="lan-right">
      <div class="code-block">
        <code-block :code="code"/>
      </div>
    </div>
    <Modal
      v-model="newPageModal"
      title="新建页面"
      :loading="newPageLoding"
      @on-ok="handlePageNew">
      请输入页面名称：<Input v-model="pageName" style="margin-top: 5px"></Input>
    </Modal>
    <Modal
      v-model="deletePageModal"
      title="删除页面"
      @on-ok="deletePage">
      确定删除页面<span v-if="curContextMenuPage">{{ curContextMenuPage.name }}</span>?
    </Modal>
  </div>
</template>

<script>
  import { getDataTranslation } from '@/apis/translate'
  import EditableTable from '@/components/editableTable';
  import codeBlock from '@/base/codeBlock'
  import { generateUUID, downloadAsJson } from '../utils/assist'
  
  export default {
    components: {
      EditableTable,
      codeBlock,
    },
    data () {
      return {
        pages: [],
        curContextMenuPage: null,
        curPage: null,
        newPageModal: false,
        deletePageModal: false,
        newPageLoding: true,
        pageName: '',
        multiLoading: false,
        code: '',
        key_setting: {
          zh: 'zh',
          en: 'en'
        }
      }
    },
    created () {
      let pages = localStorage.getItem('pages')
      if (pages) {
        this.pages = JSON.parse(pages)
        this.curPage = this.pages[0]
      }
      console.log(this.pages, 'created', this.curPage)
    },
    methods: {
      handleItemClick (item) {
        this.curPage = item
        this.code = ''
      },
      handleNewPage () {
        this.newPageModal = true
      },
      handlePageDelete () {
        if (!this.curContextMenuPage) {
          return
        }
        console.log(this.curContextMenuPage)
        this.deletePageModal = true
      },
      // new page
      handlePageNew () {
        if(this.pageName.trim() === ''){
          this.$Message.error('页面名称不合法')
          this.newPageLoding = false
          this.$nextTick(() => {
            this.newPageLoding = true
          })
        } else {
          this.$Message.success('新建成功')
          this.pages.push({
            name: this.pageName,
            data: [{
              key: '',
              items: [{
                key: '',
                value: ''
              }]
            }],
            id: generateUUID()
          })
          this.pageName = ''
          this.curPage = this.pages[this.pages.length - 1]
          localStorage.setItem('pages', JSON.stringify(this.pages))
          this.newPageModal = false
        }
      },
      clear () {
        this.curPage.data = []
      },
      save () {
        let obj = {
          name: this.curPage.name,
          data: this.curPage.data,
          id: this.curPage.id
        }
        console.log(obj, localStorage)
        for ( let i = 0; i < this.pages.length; i++) {
          if ( this.pages[i].id === obj.id ) {
            this.pages[i] = obj
            break
          }
        }
        localStorage.setItem('pages', JSON.stringify(this.pages))
      },
      set () {
        this.getDataEng(this.curPage.data)
      },
      async multi () {
        this.multiLoading = true
        let _en_data = await this.getDataEng(this.curPage.data)
        console.log(_en_data, 'eng data')
        let zh_json = this.data2json(this.curPage)
        let en_json = this.data2json({
          name: this.curPage.name,
          data: _en_data
        })
        let multiObj = {
          [this.key_setting.zh]: zh_json,
          [this.key_setting.en]: en_json
        }
        this.code = multiObj
        this.multiLoading = false
      },
      change () {
        this.code = this.data2json(this.curPage)
        console.log(this.code, 'origin code')
      },
      handleTableAdd() {
        this.curPage.data.push({
          key: '',
          items: [{
            key:'',
            value: ''
          }]
        })
      },
      // data数组返回json格式
      data2json(page){
        let obj = {}
        obj[page.name] = {}
        page.data.forEach(o => {
          if(!o.key){
            o.items.map(item => {
              if(item.key){
                obj[page.name][item.key] = item.value
              }
            })
          }else{
            let temp  = {}
            o.items.map(item => {
              if(item.key){
                temp[item.key] = item.value
              }
            })
            obj[page.name][o.key] = temp
          }
        })
        return obj;
      },
      // 获取data的英文
      async getDataEng (data) {
        try {
          let res = await getDataTranslation(data)
          console.log(res.data.data, 'get data eng success')
          return res.data.data
        } catch (e) {
          console.log(e, 'get data eng fail')
        }
      },
      test (item) {
        this.curContextMenuPage = item
        console.log(item.id, 'context-menu')
      },
      deletePage () {
        for (let i = 0; i < this.pages.length; i++) {
          if (this.pages[i].id === this.curContextMenuPage.id) {
            this.pages.splice(i, 1)
            console.log(this.pages, 'deleted')
            this.curPage = this.pages.length > 0 ? this.pages[0] : null
            this.$Message.success('删除成功')
            return
          }
        }
      },
      download () {
        console.log(this.code)
        // this.$refs.xxx.innerHTML = JSON.stringify(this.code)
        downloadAsJson(this.code, 'haha')
      },
      handleContextmenu (vnode) {
        console.log('被点击区域的 vnode:', vnode)
      },
    }
  }
</script>

<style scoped>
.lan-wrapper{
  display: flex;
}
.lan-left{
  width: 200px;
  background: #FAFAFA;
  cursor: pointer;
}
.lan-middle{
  flex: 1;
  padding: 20px;
  border-right: 1px solid rgb(177, 175, 175);
}
.lan-middle-nocontent{
  padding-top: 160px;
}
.lan-middle-content{
  overflow-y: auto;
  height: 100%;
}
.edit-table-wrapper{
  height: 100%;
}
.left-header{
  height: 40px;
  line-height: 40px;
  color: #FD783B;
  padding: 0 50px;
  text-align: left;
  border-bottom: 1px solid #ccc;
}
.page-item{
  height: 30px;
  line-height: 30px;
}
.active-page-item{
  background: rgb(185, 182, 182);
}
.edit-table{
  margin: 0 auto 10px auto;
}
.lan-right{
  flex: 1;
  padding: 20px;
}
.code-block{
  height: 100%;
}
</style>


