<template>
  <div>
    <fra class="aaa"></fra>
    <p @click="test">路由测试</p>
    <div class="test" placeholder="red" a="999">test</div>
    <router-view/>
  </div>
</template>

<script>
import fra from '../components/fra'
  export default {
    components: {
      fra
    },
    data () {
      return {
      }
    },
    mounted() {
      console.log(this.$children,this.$options)
      console.log('22',this.$parent)
      alert(window.env.a)
    },
    methods: {
      test() {
        this.$router.push(`frag/aa`)
      }
    }
  }
</script>

<style scoped>
.test:before{
  content: attr(a);
  color: attr(placeholder);
}
</style>


