<template>
  <div>
    <p @click="test">测试</p>
    <p>{{ obj.a.b }}</p>
    <p v-for="item in arr" :key="item">{{item}}</p>
  </div>
</template>

<script>
export default {
  components: {
    
  },
  data () {
    return {
      x: 'initial',
      obj: {
        a: {
          b: 34
        }
      },
      arr: [{
        name: 'haha'
      }],
      a: {
        a: 11,
        b: 22
      }
    };
  },
  watch: {
    x (val) {
      console.log(val, 'x change')
    }
  },
  async created(){
    let t = new Date().getTime()
    let a =5
    while(a-->0){
        let s = new Date().getTime()
        console.log(`get report after ${(s - t) / 1000}s`)
        console.log(a, '111111')
        setTimeout(() => {
            console.log(a, '222222')
        })
        await this.sleep(1)
        console.log(a, '33333')
    }
    /* for (let i = 0; i < 5; i++) {
      let s = new Date().getTime()
      console.log(`for loop time ${(s - t) / 1000}s`, i)
      this.sleep(1000)
    } */
  },
  mounted () {
    /* console.log('mounted', this.x) */
  },
  methods: {
    test(){
      this.obj = this.change()
      this.arr[0].name = '2222'
      this.a.c = 333
    },
    change () {
      return {
        a: {
          b: 'change by func'
        }
      }
    },
    sleep(time){
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve()
        }, time)
      })
    }
  }
}
</script>

<style scoped>

</style>


