import Stack from '../utils/Stack'
const FLAG = '#'

// return 开头#的个数 to-improve
export function getPoundNum (item) {
  let re = /^(#+)/
  let arr = item.match(re)
  return arr && arr.length > 0 ? arr[0].length : 0
}

// 验证当前项开头是否只含有一个#
function isFirstNode(str) {
  return getPoundNum(str) === 1
}

// 去掉key开头的一个或者多个#
function validKey (str) {
  return str.replace(/^(#+)/, '')
}

// esonText -> arr(删除空行, 后期：叶子结点是否处理，怎么处理)
export function esonText2arr (esonText) {
  let iMixMode = false // 是否是混合模式
  let arr = esonText.split('\n')
  let _tempArr = []
  arr.forEach(item => {
    item = item.trim()
    if (item) {
      _tempArr.push(item)
      if (item.startsWith('#')) {
        iMixMode = true
      }
    }
  })
  return {
    esonArr: _tempArr,
    iMixMode
  }
}

// 校验规则5
// 5. 假设第i个节点含有n个#，那么下一个分支节点的#个数为(1, n + 1)
export function validStructrue (arr, iMixMode) {
  let parent = null
  /* if (iMixMode && !isFirstNode(arr[0])) {
    throw new Error('第一行必须以一个#开头')
  } */
  for (let i = 0; i < arr.length; i++) {
    let node = arr[i]
    if (node.startsWith('#')) {
      if (!parent) {
        parent = node
      } else {
        let parentPoundNum = getPoundNum(parent)
        let nodePoundNum = getPoundNum(node)
        if (nodePoundNum > parentPoundNum + 1) {
          throw new Error(`Invalid Structrue:${node}`)
        }
        parent = node
      }
    }
  }
}

// arr -> json
export function arr2json(arr, iMixMode) {
  let json = {}
  if (!iMixMode) {
    arr.forEach(item => {
      let _temp = item.split(':') // 假设值格式为a:33
      json[_temp[0]] = _temp[1]
    })
    return json
  }
  let stack = new Stack()
  for (let i = 0; i < arr.length; i++) {
    let item = arr[i]
    let topNode = stack.getTop()
    if (item.startsWith('#')) {
      let num = getPoundNum(item)
      let key = validKey(item)
      // 检查栈里面的元素,同级出栈，低级入栈，高级多个出栈
      if (topNode === null) {
        stack.push({
          name: item,
          children: {},
          key: validKey(item)
        })
      } else {
        let topNum = getPoundNum(topNode.name)
        if (topNum === num) {
          let outNode = stack.pop()
          // 检测键重复
          if (outNode.name === item) {
            throw new Error(`Repeat key: ${item}`)
          }
          // 判断outNode是否为一个#开头的行
          if (getPoundNum(outNode.name) === 1) {
            let key = validKey(outNode.name)
            json[key] = outNode.children
          } else {
            let topNode = stack.getTop()
            topNode.children = Object.assign(topNode.children, {
              [outNode.key]: outNode.children
            })
          }
        }
        if (topNum > num) {
          while (topNum >= num) {
            let outNode = stack.pop()
            // 检测键重复
            if (topNum === num) {
              console.log(outNode, item)
              if (outNode.name === item) {
                throw new Error(`Repeat key: ${item}`)
              }
            }
            // 判断outNode是否为一个#开头的行
            if (getPoundNum(outNode.name) === 1) {
              let key = validKey(outNode.name)
              json[key] = outNode.children
              topNum = 0
            } else {
              let topNode = stack.getTop()
              topNode.children = Object.assign(topNode.children, {
                [outNode.key]: outNode.children
              })
              topNum = getPoundNum(stack.getTop().name)
            }
          }
        }
        stack.push({
          name: item,
          children: {},
          key: validKey(item)
        })
      }
    } else {
      let _temp = item.split(':') // 假设值格式为a:33
      if (topNode) {
        topNode.children = Object.assign(topNode.children, {
          [_temp[0]]: _temp[1]
        })
      } else {
        json[_temp[0]] = _temp[1]
      }
    }
  }
  while (stack.getLength() > 0) {
    let outNode = stack.pop()
    if (isFirstNode(outNode.name)) {
      let key = validKey(outNode.name)
      json[key] = outNode.children
    } else {
      let nextNode = stack.getTop()
      nextNode.children = Object.assign(nextNode.children, {
        [outNode.key]: outNode.children
      })
    }
  }
  /* console.log(stack) */
  /* let root = stack.getTop()
  json[root.key] = root.children */
  return json
}


