import Vue from 'vue'
import Router from 'vue-router'
import arrayObject from '@/components/arrayObject'
import lan from '@/pages/lan'
import lan2 from '@/pages/lan2'
import test from '@/pages/test'
import buffer from '@/pages/buffer'
import json1 from '@/pages/json1'
import test1 from '@/pages/test1'
import test2 from '@/pages/test2'
import file from '@/pages/file'
import easyJson from '@/pages/easyJson'
import tree from '@/pages/tree'
import idg from '@/pages/idg'
import table from '@/pages/table'
import fragment from '@/pages/fragment'
import JSONHandle from '@/pages/JSONHandle'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'arrayObject',
      component: arrayObject
    },{
      path: '/lan',
      component: lan2
    },{
      path: '/test',
      component: test
    },{
      path: '/buffer',
      component: buffer
    },
    {
      path: '/json',
      component: JSONHandle
    },
    {
      path: '/json1',
      component: json1
    },
    {
      path: '/test1',
      component: test1
    },
    {
      path: '/test2',
      component: test2
    },
    {
      path: '/language',
      component: lan2
    },
    {
      path: '/file',
      component: file
    },
    {
      path: '/table',
      component: table
    },
    {
      path: '/frag',
      component: fragment,
      children: [
        {
          path: ':type',
          component: file
        }
      ]
    },
    {
      path: '/idg',
      component: idg
    },
    {
      path: '/easyJson',
      component: easyJson
    }
  ]
})



