<template>
  <div id="app">
    <div class="topbar">
      <router-link to="/" class="topbar-item" tag="div">表格转JSON</router-link>
      <router-link to="/lan" class="topbar-item" tag="div">多语言转换</router-link>
      <router-link to="/buffer" class="topbar-item" tag="div">Buffer转换</router-link>
      <router-link to="/json" class="topbar-item" tag="div">json展示</router-link>
      <!-- <router-link to="/json1" class="topbar-item" tag="div">height测试</router-link>
      <router-link to="/test1" class="topbar-item" tag="div">test11</router-link>
      <router-link to="/test2" class="topbar-item" tag="div">test111</router-link> -->
      <!-- <router-link to="/language" class="topbar-item" tag="div">language</router-link> -->
      <router-link to="/file" class="topbar-item" tag="div">file test</router-link>
      <router-link to="/table" class="topbar-item" tag="div">table</router-link>
      <router-link to="/frag" class="topbar-item" tag="div">fragment</router-link>
      <!-- <router-link to="/idg" class="topbar-item" tag="div">idg</router-link> -->
      <router-link to="/easyJson" class="topbar-item" tag="div">easyJson</router-link>
    </div>
