<script>
export default {
  name: 'App',
}
</script>

<style>
html, body{
  height: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(238, 238, 238);
  text-align: center;
  height: 100%;
}
.topbar{
  display: flex;
  height: 50px;
  background: #8f9caa;
  text-align: left;
  padding-left: 20px;
}
.topbar-item{
  line-height: 50px;
  color: black;
  margin-right: 20px;
}
.content{
  height: calc(100vh - 50px);
}
</style>



