<template>
  <div class="buffer-wrapper">
    <div class="buffer-left">
      <Input v-model="custom_input" type="textarea" placeholder="Enter something..." :rows="16"/>
      <div class="left-button-wrapper">
        <Button type="primary" @click="clear">清空</Button>
        <Button type="primary" @click="buffer2string">Buffer转成string</Button>
        <Button type="primary" @click="uni2zh">Unicode转中文</Button>
      </div>
    </div>
    <div class="buffer-right">
      <Icon type="ios-arrow-back" class="back-arrow" size="20" @click="shift2left"/>
      <Input v-model="custom_output" type="textarea" placeholder="result..." :rows="16"/>
    </div>
  </div>
</template>

<script>
export default {
  components: {
    
  },
  data () {
    return {
      custom_input: '',
      custom_output: ''
    };
  },

  methods: {
    clear(){
      this.custom_input = ''
      this.custom_output = ''
    },
    buffer2string () {
      if(this.custom_input === ''){
        this.$Message.error('请输入buffer数组(十进制)！')  // JSON.parse解析其它进制数组出错
        return
      }
      try{
        let buffer = new Buffer(JSON.parse(this.custom_input)) //[0xE4,0xB8,0xAD]
        this.custom_output = buffer.toString()
        console.log(this.custom_output)
      }catch(e){
        this.custom_output = ''
        this.$Message.error('请输入buffer数组(十进制)！')
      }
      
    },
    shift2left(){
      this.custom_input = this.custom_output
      this.custom_output = ''
    },
    uni2zh(){
      // this.custom_output = decodeURI(encodeURI(this.custom_input))
      this.custom_output = this.decodeUnicode(this.custom_input)
    },
    decodeUnicode (str) {
      str = str.replace(/\\/g, '%')
      return unescape(str)
    }
  }
}
</script>

<style scoped>
.buffer-wrapper{
  padding: 20px;
  display: flex;
}
.buffer-left{
  flex: 1;
  margin-right: 100px;
}
.left-button-wrapper{
  margin-top: 20px;
}
.buffer-right{
  position: relative;
  flex: 1;
}
.back-arrow{
  position: absolute;
  top: 150px;
  left: -60px;
  padding: 5px;
  background: rgb(175, 172, 172);
}
</style>


