<template>
  <div class="wrapper">
    <div class="left-bar"></div>
    <div class="middle">
      <div class="top-bar">
        <Button type="primary" @click="newTable">预览页面代码</Button>
      </div>
      <Table :columns="columns" v-if="showTable"></Table>
    </div>
    <div class="right-setting">
      <Card title="数据配置" style="margin-bottom: 20px">
        <Form :label-width="100">
          <FormItem label="使用默认配置">
            <RadioGroup v-model="tableSetting.useDefaultSetting">
              <Radio label="yes"><span>是</span></Radio>
              <Radio label="no"><span>否</span></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="Input">
            <Input v-model="tableSetting.columnName" placeholder="Enter something..."></Input>
          </FormItem>
          <FormItem label="数据变量名：" v-if="!tableSetting.useDefaultSetting">
            <Input v-model="tableSetting.columnName" placeholder="Enter something..."></Input>
          </FormItem>
          <FormItem label="列值：">
            <Button type="primary" size="small" @click="newColumn">编辑</Button>
          </FormItem>
        </Form>
      </Card>
      <Card title="样式配置" style="margin-bottom: 20px">
      </Card>
      <Button type="primary" @click="newTable">生成组件</Button>
    </div>
    <Modal
      v-model="showNewColumnModal"
      title="生成列">
      <arrayObject2 :ths="['key', 'title']" v-model="columnValue"/>
    </Modal>
  </div>
</template>

<script>
import hTable from '@/components/table/table'
import arrayObject2 from '@/components/arrayObject2'
export default {
  components: {
    arrayObject2
  },
  data () {
    return {
      tableSetting: {
        useDefaultSetting: 'yes',
        columnName: '',
        dataName: '',
        margin: ''
      },
      showNewColumnModal: false,
      columnValue: [],
      columns: [],
      showTable: false
    }
  },
  methods: {
    newColumn () {
      this.showNewColumnModal = true
    },
    newTable () {
      this.columns = _.cloneDeep(this.columnValue)
      this.showTable = true
    }
  }
}
</script>

<style scoped>
.wrapper{
  display: flex;
}
.left-bar{
  width: 300px;
  height: 100%;
}
.middle{
  flex: 1;
  height: 100%;
  border-left: 1px solid #ccc;
  border-right: 1px solid #ccc;
}
.top-bar{
  height: 50px;
  text-align: left;
  padding: 10px 40px;
  border-bottom: 1px solid #ccc;
}
.right-setting{
  width: 260px;
  height: 100%;
}
</style>


