<template>
  <div class="bbb">
    <div v-for="item in arr" :key="item" @click="test">
      {{ item }}
    </div>
    <p :class="a">111111</p>
    <transition name="fade">
      <p v-if="x" style="color:red">2222222</p>
    </transition>
    <p>huashi</p>
    <div v-if="showWrapper">
      <h1>h1 elementw</h1>
      <Modal
        v-model="show"
        title="1111"
        >
      </Modal>
    </div>
    <Modal
        v-model="show"
        title="222222"
        >
      </Modal>
  </div>
</template>

<script>
  export default {
    components: {
    },
    data () {
      return {
        arr: [1,2,3],
        obj: {
          a: 111
        },
        x: true,
        show: false,
        showWrapper: false
      }
    },
    computed: {
      a () {
        return {
          'a': true,
          'aaa': this.x
        }
      }
    },
    watch: {
      obj (val) {
        console.log(val, 'obj')
        this.$set(this.obj, 'c', 33)
      }
    },
    methods: {
      test () {
        this.x = !this.x
        this.$set(this.obj, 'b', 33)
        this.showWrapper = true
        this.show = true
      }
    }
  }
</script>

<style scoped>
.a{
  margin-bottom: 23px;
}
.aaa{
  margin-bottom: 5px;
}
</style>


