<template>
  <div class="lan">
    <div class="lan-edit-wrapper">
      <div class="lan-left">
        <Input v-model="root" placeholder="页面名称" class="root-input"/>
      </div>
      <div class="lan-right">
        <!-- <Button @click="add" type="primary" style="margin:0 10px 10px 0">增加二级对象</Button> -->
        <div style="margin-bottom: 10px">
          <Button @click="clear" type="primary" class="button">清除</Button>
          <Button @click="set" type="primary" class="button">配置导出</Button>
          <Button @click="change" type="primary" class="button">转换</Button>
          <Button @click="multi" type="primary" :loading="multiLoading">生成多语言</Button>
        </div>
        <EditableTable
          v-for="(item, index) in data"
          :key="index"
          :data="item"
          :index='index'
          @edit-table-change="add"
          class="edit-table"/>
      </div>
    </div>
    <div class="code-block">
      <code-block :code="code"/>
    </div>
    <Modal title="配置导出" v-model="settingModal" width="400">
      <Form :label-width="120">
        <FormItem label="导出的中文key：">
          <Input v-model="key_setting.zh" placeholder="key" style="width: 200px"/>
        </FormItem>
        <FormItem label="导出的英文key：">
          <Input v-model="key_setting.en" placeholder="key" style="width: 200px"/>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
import jsonp from 'jsonp'
import axios from 'axios'
import EditableTable from '@/components/editableTable';
import codeBlock from '@/base/codeBlock'
export default {
  components: {
    'code-block': codeBlock,
    EditableTable
  },
  data () {
    return {
      root: '',
      data: [{
        key: '',
        items: [{
          key: '',
          value: ''
        }]
      }],
      code: {},
      multiLoading: false,
      key_setting: {
        zh: 'zh',
        en: 'en'
      },
      settingModal: false
    };
  },
  created(){
    console.log(Buffer,'Buffer')
  },
  methods: {
    add(index){
      if(!this.data[index + 1]){
        this.data.push({
          key: '',
          items: [{
            key:'',
            value: ''
          }]
        })
      }
    },
    clear(){
      this.root = ''
      this.data = [{
        key: '',
        items: [{
          key: '',
          value: ''
        }]
      }]
      this.code = ''
    },
    change() {
      this.code = this.data2json(this.data)
      console.log(this.code, 'origin code')
    },
    // 生成多语言
    async multi(){
      this.multiLoading = true
      let _en_data = await this.data2eng(this.data)
      console.log(_en_data, 'eng data')
      let zh_json = this.data2json(this.data)
      let en_json = this.data2json(_en_data)
      let multiObj = {
        [this.key_setting.zh]: zh_json,
        [this.key_setting.en]: en_json
      }
      this.code = multiObj
      this.multiLoading = false
    },
    set(){
      this.settingModal = true
    },
    //获取英文
    async getEng(word){
      // try catch
      let res = await axios.get(`/trans/?w=${word}`)
      return res.data
    },
    // data数组转英文
    async data2eng(){
      let eng_data = []
      for(let i = 0; i < this.data.length; i++){
        let o = this.data[i]
        let key = o.key
        let _clone_items = [] 
        for(let j = 0; j < o.items.length; j++){
          let item = o.items[j]
          let _clone_item = {
            key: item.key,
            value: item.value
          }
          if(item.key){
            let res = await this.getEng(item.value)
            if(res.status === 0){
              _clone_item.value = '出错，请检查!'
            }else{
              _clone_item.value = res.content.out || ''
            }
          }
          _clone_items.push(_clone_item)
        }
        eng_data.push({
          key,
          items: _clone_items
        })
      }
      return eng_data
    },
    // data数组返回json格式
    data2json(data_arr){
      let obj = {}
      //this.$set(obj, this.root, {})
      obj[this.root] = {}
      data_arr.forEach(o => {
        if(!o.key){
          o.items.map(item => {
            if(item.key){
              this.$set(obj[this.root], item.key, item.value)
            }
          })
        }else{
          let temp  = {}
          o.items.map(item => {
            if(item.key){
              this.$set(temp, item.key, item.value)
            }
          })
          this.$set(obj[this.root], o.key, temp)
        }
      })
      return obj;
    }
  },

}
</script>

<style scoped>
.lan{
  display: flex;
  padding: 20px;
}
.lan-edit-wrapper{
  flex: 1;
  justify-content: center;
  display: flex;
}
.lan-left{
  display: flex;
  align-items: center;
  width: 120px;
}
.button{
  margin-right: 10px;
}
.root-input{
  width: 100px;
}
.lan-right{
  overflow-y: auto;
}
.edit-table{
  margin-bottom: 10px;
}
.code-block{
  margin-left: 100px;
  flex: 1;
}
</style>



